/* Main layout */
body{
  background-color: #b7d2ff;
}

a {
  text-decoration: none;
  color: black;
}

.App-header {
  width: 100%;
  height: 50px;
}


#search {
  margin-left: 30px;
  margin-top: 20px;
  float: left;
  font-size: 15px;
  border-radius: 10px;
  margin-bottom: 10px;
  height: 35px;
  width: 35%;
}

#sub_btn {
  margin-left: 20px;
  margin-top: 22.5px;
  float: left;
  background-color: inherit;
  border: 1px solid;
  border-radius: 8px;
  height: 30px;
  width: 5%;
}

#choose {
  margin-top: 22.5px;
  margin-left: 10px;
  float: left;
  height: 15px;
  width: 50px;
}

#sub_btn:hover {
  font-weight: bold;
  border: 2px solid;
}

#powered_image {
  margin-left: 5%;
  height: 80px;
  width: 25%;
  float: left;
}

#powered_image:hover{
  cursor: pointer;
}

.forecast-body {
  border-radius: 15px;
  padding-left: 20px;
  background-color: #ffffff;
  width: 30%;
  margin-top: 10px;
  float: left;
  margin-left: 5%;
  clear: both;
}

#temperature {
  float: left;
  font-size: 30px;
  margin-bottom: 3px;
}


#forecast-body-icon {
  margin-top: 10px;
  margin-left: 30px;
  float: left;
  display: block;
  width: 90px;
  height: 90px;
}

.rain-percipity-para-daily {
  float: left;
  margin-left: 27.5%;
  width: 45%;
}

.rain-percipity-para-current {
  float: left;
}

.rain-percipity-para-current::first-letter, .rain-percipity-para-daily::first-letter {
  text-transform: capitalize;
}


.rain-percipity-icon {
  float: left;
  margin-top: 6px;
  display: block;
  width: 10%;
  height: 40px;
}

.after-percipity {
  clear: both;
}

#after-temperature {
  clear: both;
}

#description {
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 18px;
}


.forecast-detailed {
  border-radius: 15px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10px;
  margin-left: 1%;
  background-color: #ffffff;
  width: 20%;
  float: left;
  margin-left: 2.5%;
}


.item-icon {
  margin-bottom: -8px;
  margin-right: 5px;
  width: 32px;
  height: 32px;
}

.forecast-daily {
  border-radius: 15px;
  display: block;
  margin-top: 10px;
  margin-left: 5%;
  margin-bottom: 20px;
  width: 30%;
  background-color: #ffffff;
  float: left;
}


.forecast-daily h3 {
  padding-left: 20px;
  padding-bottom: 10px;
  padding-top: 10px;
}

.forecast-daily-item {
  text-align: center;
  padding: 20px;
  margin-left: 5px;
  margin-right: 5px;
  border-top: 2px solid #dbd9d9;
}


.item_btn {
  font-size: 20px;
  width: auto;
  border: none;
  background-color: inherit;
}

.item_btn:hover{
  cursor: pointer;
  font-weight: bold;
}

.footer{
  clear: both;
  padding: 10px;
  margin-top: 15px;
  display: block;
  text-align: center;
  border-top: 1px solid black;
  width: 100%;
}


@@media only screen and(min-width: 1024px) {

}



